<template>
<section class="center-best-goals">
  <div class="container mx-auto p-1 sm:mx-1 max-w-screen-md">
    <div class="w-full h-auto">
      <div class="flex flex-col align-center space-y-2">
        <div class="flex flex-row align-center justify-end w-full">
          <div>

          <span class="text-sm font-thin text-slate-700 ml-4">پنالتی</span>

          </div>
          <div>
          <span class="text-sm font-thin text-slate-700">گل</span>
          </div>
        </div>
        <div v-for="items in list" :key="items.id" class="w-full">
          <div v-for="(value,index) in items" :key="value">
            <div class="flex flex-row align-center justify-between m-3 w-full">
              <div class=" w-9/12 flex flex-row my-2">
                <span class="text-black font-thin">{{index+=1}}</span>
                <img :src="`${value.player.image}`" @error="notShow"  class="w-10 h-10 rounded-full mx-2" alt="">

                <div class="flex flex-col space-y-1">
                  <div><p class="text-black font-thin">{{value.player.name_fa}}</p></div>
                  <div class="flex flex-row">
                    <img :src="`${value.team.logo}`" @error="notShow" alt="" class="w-5 h-5 rounded-full"/>
                    <p class="text-black font-thin">{{value.team.name_fa}}</p>
                  </div>
                </div>
              </div>
              <div class=" w-3/12 flex flex-row align-center justify-around">
                <div><span class="font-thin text-black text-start">{{value.penalty}}</span></div>
              <div><span class="font-thin text-black text-start">{{value.goal}}</span></div>
              </div>
            </div>
          </div>
        </div>
        <Suspense>
          <template #default>
            <NumberGoals :menu="list"/>
          </template>
          <template #fallback>
            <LoaderComponentTwo/>
          </template>
        </Suspense>
      </div>
    </div>
  </div>
</section>
</template>

<script>

import {onMounted,reactive} from "vue";
import {defineAsyncComponent} from "vue";
import axios from 'axios'
const NumberGoals=defineAsyncComponent(()=>import('@/components/main/competitions/NumberGoals'))
const LoaderComponentTwo=defineAsyncComponent(()=>import('@/components/composable/LoaderComponentTwo'))
import {handleError} from "@/components/composable/handleError";
export default {
  name: "BestGoals",
  components: {LoaderComponentTwo, NumberGoals},
  setup(){
    const list=reactive([])
    const result=onMounted(async ()=>{
     await axios.get(`https://api.footballi.net/api/v2/competition/14/homepage?version=2`)
          .then((response)=>{
            if(response.data.success){
              list.push(response.data.data.goal)
            }

          })
          .catch((error)=>{
           handleError(error)
          })
    })
    return{
   list,result
    }
  }
}
</script>

<style scoped>

</style>